$mobile-width: 320px;
$small-device-width: 576px;
$nav-tel:374px;
$tablet-width: 768px;
$responsiveDesctop: 991px;
$desktop-width: 1024px;

@mixin mobile {
  @media (min-width: #{$mobile-width}) and (max-width: #{$small-device-width - 1px}) {
    @content;
  }
}
@mixin nav-tel {
  @media (min-width: #{$nav-tel}) and (max-width: #{$small-device-width - 1px}) {
    @content;
  }
}
@mixin small-device {
  @media (min-width: #{$small-device-width}) and (max-width: #{$tablet-width - 1px}) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
    @content;
  }
}
@mixin smallDesktop {
  @media (min-width: #{$responsiveDesctop}) and (max-width: #{$desktop-width}) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: #{$desktop-width + 1px}) {
    @content;
  }
}