.main-container{
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  background: #fbfbfb;
  overflow: hidden;
  &:before, &:after{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-repeat: no-repeat;
  }
  &:before{
    bottom: -35%;
    background-image: url("../img/bg_layerTwo.svg");
    background-size: cover;
    position: fixed;
    opacity: .7;
  }
  &:after{
    bottom: -20%;
    background-image: url("../img/bg_layerOne.svg");
    background-size: cover;
    opacity: .7;
    position: fixed;
  }
}
p{
  font-size: 12pt;
  line-height: 20pt;
  color: #666666;
}
a{
  color: $titleColor;
  transition: $transition;
  &:hover{
    color: #555070;
    text-decoration: none;
  }
}
//------- header start ---------------
header{
  padding:10px 25px 0 25px;
  z-index: 2;
  @include mobile{
    padding: 10px 15px 0;
  }
  .navbar-light{
    padding: 0;
    .nav-logo{
      font-family: $logo-font;
      font-size: 14pt;
      line-height: 21pt;
      color: $textColor;
      @include mobile{
        font-size: 12pt;
        line-height: 18pt;
      }
      span{
        font-family: $logo-font;
        position: relative;
        font-size: 22pt;
        @include mobile{
          font-size: 18pt;
        }
        &:before{
          position: relative;
          width: 100%;
          height: 1px;
          content: '';
          display: flex;
        }
      }
    }

  }
  .navbar-collapse{
    @include mobile{
      background-color: white;
      padding: 15px;
      border-radius: 10px;
      box-shadow: 0 10px 20px 0 rgba(0,0,0,.12);
    }
  }
  .separate{
    color: $textColor;
    display: flex;
    @include mobile{
      display: none;
    }
  }
  .nav-item{
    margin-right: 20px;
    font-weight: 300;
    @include smallDesktop{
      margin-right: 0;
    }
    &:last-child{
      margin-right: 10px;
    }
  }
  .nav-link{
    font-size: 11.5pt;
    line-height: 18pt;
    color: $textColor;
  }
  .currency{
    position: relative;
    border: 1px solid #c7c7c7;
    border-radius: 50px;
    padding: 5px 15px;
    margin-left: 8px;
    @include mobile{
      margin-left: 0;
      width: 100%;
      max-width: 110px;
    }
    &__item{
      color: $textColor;
      align-items: center;
      font-size: 11pt;
      line-height: 18pt;
      display: none;
      cursor: pointer;
      .flag{
        font-family: 'icomoon' !important;
        margin-left: 15px;
        font-size: 28px;
      }
      &.active{
        display: flex;
      }
    }
    .currencyDropdown{
      display: none;
      transition: $transition;
      background: lightgrey;
      padding: 5px 8px;
      border-radius: 6px;
      &.active{
        display: flex;
        flex-direction: column;
        position: absolute;
        justify-content: center;
        top: 43px;
        right: 0;
        z-index: 3;
        transition: $transition;
        .currency__item{
          display: flex;
          margin-top: 10px;
          &:last-child{
            margin-bottom: 10px;
          }
          div{
            min-width: 35px;
            text-align: center;
          }
        }
      }
    }
  }
}
//------- header end ---------------

//------- content start ---------------
.main-section{
  position: relative;
  flex-grow: 4;
  z-index:1;
  h1{
    font-size: 28pt;
    line-height: 1.3;
    font-weight: 700;
    text-align: center;
    color: $titleColor;
    margin: 10px 0 20px 0;
    @include mobile{
      font-size: 16pt;
    }
  }

  .booking-form{
    background: white;
    max-width: 100%;
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    min-height: 400px;
    box-shadow: 0 15px 27px 0 rgba(0,0,0,.14);
    transition: $transition;
    @include mobile{
      flex-wrap: wrap;
      background: transparent;
      overflow: visible;
      box-shadow: none;
    }
    &__left{
      width: 45%;
      padding: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: $transition;
      @include mobile{
        width: 100%;
        padding: 15px;
        margin-bottom: 10px;
        background: white;
        box-shadow: 0 15px 27px 0 rgba(0,0,0,.14);
        border-radius: 10px;
      }
      &.fullWidth{
        width: 100%;
      }
    }
    &__right{
      width: 55%;
      padding: 30px;
      background: $purpleGradient;
      transition: $transition;
      @include mobile{
        width: 100%;
        padding: 15px;
      }
    }
    .formChoose__active{
      margin-top: 40px;
      background: $purpleGradient;
      border-radius: 100px;
      padding: 10px 45px;
      display: inline-flex;
      text-transform: uppercase;
      color: white;
      font-size: 11pt;
      font-weight: 300;
      cursor: pointer;
      transition: $transition;
      position: relative;
      z-index: 3;
      @include mobile{
        margin: 15px 0;
      }
      &.hidden{
        transform: translateY(100%);
        overflow: hidden;
        height: 0;
        position: absolute;
        z-index: -1;
      }
    }
    .formChoose__hidden{
      height: 0;
      position: relative;
      overflow: hidden;
      transition: $transition;
    }
    &.active{
      overflow: visible;
      align-items: flex-start;
      box-shadow: none;
      background: transparent;
      .formChoose__hidden{
        height: auto;
        position: relative;
        overflow: visible;
        opacity: 1;
      }
      .booking-form__left{
        width: 60%;
        background: #fff;
        box-shadow: 0 15px 27px 0 rgba(0,0,0,.14);
        border-radius: 10px;
        transition: $transition;
        @include mobile{
          width: 100%;
        }
      }
    }
  }
  .tripDetail{
    margin-bottom: 10px;
    &-value{
      position: relative;
      font-size: 12pt;
      font-weight: 400;
      line-height: 1.4;
      color: white;
      padding-left: 5px;
      @include mobile{
        font-size: 10pt;
        padding-left: 2px;
      }
    }
    &__title{
      color: white;
      text-transform: uppercase;
      font-weight: 400;
      font-size: 12pt;
      margin-bottom: 5px;
    }
    &__item{
      margin-right: 20px;
      color: rgba(255,255,255,.7);
      font-weight: 300;
      font-size: 10pt;
      line-height: 35px;
      @include mobile{
        line-height: 25px;
      }
      [class^="icon-"]{
        display: inline-flex;
        width: 28px;
        font-size: 23px;
        color: rgba(255,255,255,.7);
        @include mobile{
          width: 23px;
          font-size: 20px;
        }
      }
      &:last-child{
        [class^="icon-"]{
          font-size: 18px;
          @include mobile{
            font-size: 17px;
            position: relative;
            top:2px;
          }
        }
        @include mobile{
          margin-right: 0;
          margin-bottom: 8px;
        }
      }
      @include mobile{
        margin-right: 10px;
      }
      p{
        color: white;
        margin-bottom: 10px;
        @include mobile{
          font-size: 10pt;
          line-height: 25px;
          margin-bottom: 3px;
        }
      }
    }
  }
  .formChoose{
    &__row{
      &:first-child{
        margin-bottom: 16px;
        position: relative;
      }
      label{
        width: 96%;
      }
      input{
        font-size: 12pt;
        color: #4e4e4e;
        border:0;
        background: rgba(163,163,163,.2);
        border-radius: 100px;
        padding: 10px 15px;
        width: 100%;
        @include mobile{
          font-size: 11pt;
        }
        &::placeholder{
          font-size: 10pt;
          line-height: 24px;
        }
        &:focus, &:active{
          border: none;
          box-shadow: none;
          outline: none;
        }
      }
    }
    &__button{
      margin-top: 40px;
      background: $purpleGradient;
      border-radius: 100px;
      padding: 10px 25px;
      display: inline-flex;
      text-transform: uppercase;
      color: white;
      font-size: 11pt;
      font-weight: 300;
      cursor: pointer;
      &#sendForm{
        transition: $transition;
        @include mobile{
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
        }
        &:hover{
          opacity: .7;
        }
      }
      @include mobile{
        margin-top: 10px;
        padding: 10px 30px;
      }
    }
  }
  .js-swap-direction{
    position: absolute;
    top:calc(100% - 13px);
    right: 2%;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    text-align: center;
    line-height: 38px;
    background: $purpleGradient;
    font-size: 18px;
    opacity: .9;
  }
  .formRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    border-radius: 0 10px 10px 0;
    overflow: hidden;
    @include mobile{
      border-radius: 10px;
    }
    &__total{
      position: absolute;
      right: 30px;
      bottom: 30px;
      z-index: 3;
      display: flex;
      align-items: center;
      [class^='icon-']{
        font-size: 40px;
        @include mobile{
          font-size: 30px;
        }
      }
      .totalValue{
        margin-left: 10px;
        &__title{
          margin-bottom: 0;
          font-size: 10pt;
          color: white;
          opacity: .7;
        }
        &__number{
          font-size: 14pt;
          font-weight: 700;
          color: white;
          @include mobile{
            padding-left:10px;
          }
        }
        @include mobile{
          display: flex;
        }
      }
      @include mobile{
        position: relative;
        right: auto;
        left: 0;
        bottom: 0;
        margin-top: 12px;
      }
    }
    &__map{
      .layer-svg{
        position: absolute;
        background: url("../img/above-map.svg") bottom no-repeat;
        background-size: contain;
        bottom: -10px;
        width: calc(100% + 20px);
        height: 100%;
        left: -6px;
        z-index: 2;
        @include mobile{
          display: none;
        }
      }
      #map{
        position: relative;
        border-radius: 4px;
        height: 280px;
        width: 100%;
        margin: -15px auto 0 auto;
        @include mobile{
          height:170px;
        }
      }
    }
  }
  .choose-direction{
    &__title{
      font-size: 18pt;
      font-weight: 700;
      line-height: 21pt;
      color: $textColor;
      margin-bottom: 25px;
      margin-top: 5px;
    }
    &__subtitle{
      font-size: 10pt;
      line-height: 14pt;
      color: #686868;
      opacity: .8;
      font-weight: 300;
    }
  }
  .directions-type{
    &__title{
      margin-top: 15px;
      font-size: 16pt;
      font-weight: 400;
      line-height: 1.5;
      padding: 0 20px;
      color: $textColor;
    }
    &__item{

    }
  }
  .direction-item{
    padding: 0 10px 10px 10px;
    &__img{
      position: relative;
      border-radius: 4px;
      overflow: hidden;
      margin-bottom: 5px;
      img{
        width: 100%;
      }
    }
    &__title{}
    &__button{
      position: absolute;
      bottom: 10px;
      width: 100%;
      text-align: center;
      display: none;
    }
  }


}
//------- content end ---------------
//------- input style --------
.checkInputWidth{
  width: 100%;
  max-width: 96%;
  .formChoose__button{
    margin-top: 0;
    border: 1px solid #614bb9;
    padding: 10px 25px;
    color: #624bb9;
    background: transparent;
    transition: $transition;
    &:hover{
      background: rgba(98, 75, 185, 0.85);
      color: white;
      cursor: pointer;
    }
    &:before{
      top:8px;
      @include mobile{
        top:21px;
      }
    }
    &:after{
      top:10px;
      @include mobile{
        top:23px;
      }
    }
    @include mobile{
      font-size: 10pt;
      padding: 10px;
      margin-top: 15px;
    }
  }
  @include mobile{
    max-width: 100%;
  }
}

.datetime-toFrom{
  display: flex;
  max-width: 96%;
  justify-content: space-between;
  @include mobile{
    max-width: 100%;
    flex-wrap: wrap;
  }
  &__item{
    width: 48%;
    @include mobile{
      width: 100%;
      margin-bottom: 0;
      margin-top: -10px;
    }
    .icon-stopwatch{
      position: relative;
      &:before{
        font-family: 'icomoon';
        position: absolute;
        right: 7px;
        top:calc(50% - 10px);
        font-size: 20px;
        color: #524bb6;
      }
    }
    .icon-calendar{
      position: relative;
      &:before{
        font-family: 'icomoon';
        position: absolute;
        right: 7px;
        top:calc(50% - 10px);
        font-size: 20px;
        color: #524bb6;
      }
    }
    input{
      height: 40px;
      width: 100%;
      padding: 7px 15px;
      border-radius: 100px;
      box-shadow: none;
      border: 0;
      background-color: #ededed;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
      font-size: 11pt;
      line-height: 1.5;
      &::placeholder{
        font-size: 10pt;
        line-height: 24px;
        font-weight: 400;
      }
      &:focus, &:active{
        border: none;
        box-shadow: none;
        outline: none;
      }
    }
  }
}
.datetime-toFrom-toggle{
  height: 0;
  overflow: hidden;
  display: flex;
  transition: $transition;
  &.active{
    height: 80px;
    overflow: visible;
    @include mobile{
      height: 120px;
    }
  }
}
.checkbox + label:after{
  content: '';
  position: absolute;
  top: 2px;
  right: 25px;
  width: 22px;
  height: 22px;
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0 2px 5px rgba(0,0,0,.3);
  transition: .2s;
}
.checkbox:checked + label:after {
  right: 3px;
}
.checkbox + label:before{
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 26px;
  border-radius: 13px;
  background: #CDD1DA;
  box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
  transition: .2s;
}
.checkbox:checked + label:before {
  background: rgba(98,75,185,.85);
}
input[type='checkbox']{
  opacity: 0;
  position: absolute;
}
.form-check{
  padding-left: 0;
}
.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}
.radio + label {
  position: relative;
  padding: 15px;
  border: 1px solid lightgrey;
  border-radius: 3px;
  cursor: pointer;
  opacity: 0.6;
  transition: $transition;
  &:hover{
    opacity: 0.8;
  }
}
.radio:checked + label{
  box-shadow: 0 5px 15px 0 rgba(84, 84, 84, 0.29);
  opacity: 1;
}
.input-width{
  width: 100%;
  .form-control{
    border-radius: 100px;
  }
}
.people-counter, .children{
  display: flex;
  margin-bottom: 25px;
  &__minus, &__plus{
    border-radius: 100px;
    border: 1px solid #554bb7;
    line-height: 31px;
    text-align: center;
    min-width: 35px;
    transition: all .3s;
    cursor: pointer;
    font-size: 25px;
    font-weight: 100;
    color: #584bb8;
    &:hover{
      background-color: #584bb8;
      color: white;
    }
  }
  &__minus{
    position: relative;
    &:before{
      font-family: 'icomoon';
      content: '\e94e';
      position: absolute;
      right: -50px;
      z-index: 2;
      font-size: 23px;
      line-height: 33px;
      font-weight: 100;
      color: $light-grey;
    }
    &:after{
      content: 'x';
      position: absolute;
      right: -62px;
      z-index: 2;
      font-size: 18px;
      line-height: 33px;
      color: $light-grey;
    }
  }
  &__num{
    padding: 5px 20px 5px 60px;
    margin: 0 5px;
    position: relative;
    border-radius: 100px;
    border: 1px solid #544ab7;
  }
  &__label{
    min-width: 120px;
  }
  &_child{
    .children__minus{
      position: relative;
      &:before{
        font-family: 'icomoon';
        content: '\e94d';
        position: absolute;
        right: -35px;
        z-index: 2;
        font-size: 23px;
        line-height: 30px;
        font-weight: 100;
        color: $light-grey;
      }
      &:after{
        content: 'x';
        position: absolute;
        right: -44px;
        z-index: 2;
        font-size: 18px;
        line-height: 33px;
        color: $light-grey;
      }
    }
  }
}
.custom-item-dropdown{
  margin-top: 10px;
  height: 0;
  transition: all 0.3s;
  overflow: hidden;
  &.active{
    height: 50px;
  }
  .form-control{
    font-size: 11pt;
    &::placeholder{
      opacity: .6;
    }
    &:focus, &:active{
      box-shadow: none;
      border-color: lightgrey;
    }
  }
}
.form-group-text__text{
  textarea{
    resize: none;
    font-size: 11pt;
    height: 75px;
    border-radius: 8px;
    &::placeholder{
      opacity: .6;
    }
    &:focus, &:active{
      box-shadow: none;
      border-color: lightgrey;
    }
  }
}
.form-group-number{
  width: 50%;
}
.custom-service-title{
  font-size: 12pt;
  font-weight: 700;
  line-height: 21pt;
  color: #6f6f6f;
  margin-bottom: 15px;
  margin-top: 5px;
  position: relative;
  //&:before{
  //  content: '';
  //  position: relative;
  //  height: 3px;
  //  width: 5px;
  //  background: $purpleGradient;
  //  border-radius: 100px;
  //  margin-right: 5px;
  //}
}
.last-step-item{
  &:first-child{
    flex-grow: 2;
    margin-right: 20px;
    @include mobile{
      margin-right: 0;
    }
  }
  @include mobile{
    width: 100%;
  }
}
.wrap-mobile{
  @include mobile{
    flex-wrap: wrap;
  }
}
.intl-tel-input{
  @include mobile{
    width: 100%;
  }
  input{
    @include mobile{
      width: 100%;
    }
  }
}
.last-step-item__left{
  @include mobile{
    display: none;
  }
}
.emailStyle{
  .input-group-text{
    border-radius: 100px 0 0 100px;
  }
}
.emailInput{
  border-radius: 100px !important;
}
.form-check-inline{
  display: flex;
}
input{
  &#phone{
    height: 38px;
    box-sizing: border-box;
    border-radius: 100px;
    border: 1px solid lightgray;
  }
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}
//------- input style end --------


//---------- feedback-page start -------

.feedback-slick{
  &__item{
    color: $textColor;
    margin: 10px;
    @include mobile{
      margin: 10px 0 20px 0;
    }
    &:focus, &:active{
      outline: none;
    }
  }
  .feedbackItem{
    &__feed{
      background-color: white;
      padding: 15px;
      border-radius: 10px;
      box-shadow: 0 4px 4px 5px rgba(0,0,0,.015);
      &:focus, &:active{
        outline: none;
      }
    }
    &__name{
      padding: 0 15px;
      &:focus, &:active{
        outline: none;
      }
    }
  }
}
#newFeedBack, #contactForm, #driverForm{
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 4px 5px rgba(0,0,0,.015);
  padding: 15px 20px;
  h3{
    text-transform: uppercase;
    margin-bottom: 15px;
    color: $titleColor;
    font-size: 18pt;
  }
  input{
    margin-bottom: 15px;
    border-radius: 100px;
    border: 1px solid #ced4da;
    height: 40px;
  }
  input, textarea{
    padding: 0 15px;
    font-size: 12pt;
    color: #575b5f;
    &:focus, &:active{
      outline: none;
    }
    &::placeholder{
      font-size: 11pt;
      color: #9aa0a6;
    }
  }
  textarea{
    resize: none;
    border-radius: 10px;
    border: 1px solid #ced4da;
    padding: 7px 15px;
    min-height: 100px;
  }
  .newFeedBack__button{
    margin-top: 20px;
    border: none;
    transition: $transition;
    &:hover{
      opacity: .7;
    }
    &:focus, &:active{
      outline: none;
    }
  }
}
#driverForm{
  input{
    &[type="tel"]{
      width: 100% !important;
      margin-bottom: 15px !important;
      padding-left: 55px;
    }
  }
  .flag-container{
    left:6px;
    height: 40px;
    &:active, &:focus{
      outline: none;
    }
  }
  span{
    text-align: center;
    color: $textColor;
    font-size: 10pt;
    margin-bottom: 10px;
  }
}
#namePassenger{
  width: 100%;
  border-radius: 100px;
  height: 40px;
  border: 1px solid lightgrey;
  padding: 0 15px;
  &:active, &:focus{
    outline: none;
  }
}
//---------- feedback-page end ---------
