  }
}
.mobileFooter{
  @include mobile{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  a{
    @include mobile{
      text-align: center;
      margin-left: 0;
    }
  }
  .nav-logo{
    @include mobile{
      text-align: center;
    }
  }
  &:nth-child(1){
    @include mobile{
      order: 2;
    }
  }
}
.privatePolice{
  flex-wrap: wrap;
  &:nth-child(1), &:nth-child(2){
    @include mobile{
      width: 100%;
    }
  }
  &:nth-child(1){
    a{
      &:after{
        position: absolute;
        right: 0;
        content: '|';
        @include mobile{
          content: '';
        }
      }
    }
  }
  @include mobile{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  img{
    @include mobile{
      margin-left: 0;
    }
  }
  a{
    font-size: 10pt;
    color: $textColor;
    padding: 0 10px;
    position: relative;
    transition: $transition;
    @include mobile{
      width: 100%;
      text-align: center;
      padding: 5px;
      font-size: 11pt;
    }
    img{
      @include mobile{
        margin-left: 0;
      }
    }
    &:hover{
      opacity: .7;
      text-decoration: none;
    }
    &:last-child{
      &:after{
        position: absolute;
        right: 0;
        content: '';
      }
    }
  }
}
