.messageBg{
  height: 100vh;
  width: 100vw;
  background: rgba(0,0,0,.4);
  position: fixed;
  z-index: 19;
  display: none;
  opacity: 0;
  transition: all .3s;
  &.active{
    display: block;
    opacity: 1;
    transition: all .3s;
  }
}
.formSendSuccess{
  background-color: white;
  padding: 50px 60px 30px 60px;
  border-radius: 10px;
  position: fixed;
  top:40%;
  left: 50%;
  transform: translate(-50%, -40%);
  z-index: 20;
  box-shadow: 0 15px 100px 0 rgba(0,0,0,.45);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $textColor;
  &__success{
    margin: 10px auto;
  }
