  }
  &__close{
    margin-top: 0;
    border: 1px solid #614bb9;
    padding: 10px 25px;
    color: #624bb9;
    background: transparent;
    transition: $transition;
    border-radius: 100px;
    &:hover{
      background: rgba(98, 75, 185, 0.85);
